"use client";
import { useSession, signOut } from "next-auth/react";
import { useRouter } from "next/navigation";
import { useEffect } from "react";
import Image from "next/image";

export default function Dashboard() {
  const { data: session, status } = useSession();
  const router = useRouter();
  console.log("Session data in Dashboard:", session.backendUser);

  useEffect(() => {
    if (status === "loading") return; // Still loading
    if (!session) {
      router.push("/login");
    }
  }, [session, status, router]);

  if (status === "loading") {
    return <div>Loading...</div>;
  }

  if (!session) {
    return null;
  }

  return (
    <div className="min-h-screen bg-[#fffbeb] p-8">
      <div className="max-w-4xl mx-auto">
        <div className="bg-white rounded-xl shadow-lg p-8">
          <h1 className="text-3xl font-bold text-[#3E2723] mb-6">Dashboard</h1>
          
          {/* Display user info from your backend */}
          {session.backendUser && (
            <div className="bg-[#FFFDF6] border border-[#E9DCCF] rounded-lg p-6 mb-6">
              <h2 className="text-xl font-semibold text-[#6B4F4F] mb-4">
                User Information
              </h2>
              <div className="space-y-2">
                <p><strong>Name:</strong> {session.backendUser.name}</p>
                <p><strong>Email:</strong> {session.backendUser.email}</p>
                <p><strong>Role:</strong> {session.backendUser.role}</p>
                <Image
                  src={session.backendUser.profileImage || "/default-profile.png"}
                  alt="Profile Image" 
                  width={100}
                  height={100}
                  className="rounded-full mt-4"
                />

              </div>
            </div>
          )}

          <button
            onClick={() => signOut({ callbackUrl: "/login" })}
            className="bg-[#6B4F4F] text-white px-6 py-2 rounded-md hover:bg-[#5A4038] transition-colors"
          >
            Sign Out
          </button>
        </div>
      </div>
    </div>
  );
}